.request {
  padding: 35px 0;
  background: no-repeat right center / cover;
  background-image: url(../img/bg/request.png);
  &--left {
    background-image: url(../img/bg/request-left.png);
    background-size: auto 80%;
    background-position: 0 bottom;
  }
  &__inner {
    position: relative;
  }
  &__header {
    margin-bottom: 80px;
    & > h2 {
      max-width: 550px;
    }
  }
  &__img {
    position: absolute;
    z-index: 2;
    left: -11%;
    top: 25%;
    // height: 100%;
    width: 66%;
    &--right {
      left: 41%;
      top: 33%;
    }
  }
  &__text {
    position: absolute;
    top: -2%;
    left: 36%;
    font-style: oblique;
    font-size: $fz-default;
    line-height: 18px;
    max-width: 200px;
    // animation: bounce 1s 5s infinite; //TODO manipulate intervals between repeats in JS
    &::after {
      content: '';
      display: block;
      width: 75px;
      height: 25px;
      background: no-repeat center / cover;
      background-image: url(../img/icon/arrows/short.png);
      position: absolute;
      left: 87%;
      top: 10%;
    }
    & > .undline {
      text-decoration: underline;
    }
  }
  &__form {
    max-width: 585px;
    margin: 0 0 0 auto;
    &--left {
      margin: 0 auto 0 0;
    }
  }
  &__img {
    // display: none;
  }
}

.request-form {
  $brs-inflated: 50% 50px;
  padding: 90px 0px 60px 0px;
  background-image: url(../img/content/thermal-right.png);
  background-repeat: no-repeat;
  background-size: auto;
  background-position: right 40%;
  background-color: #f1f1f1;
  border: 8px solid #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  border-top-left-radius: $brs-inflated;
  border-top-right-radius: $brs-inflated;
  border-bottom-left-radius: $brs-inflated;
  border-bottom-right-radius: $brs-inflated;

  position: relative;
  &--left-hand {
    background-image: url(../img/content/thermal-left.png);
    background-position: left 40%;
    border: 2px solid #fc9743 !important;
    background-color: #fff;

  }
  &__img {
    width: 118%;
    margin-left: -15%;
    position: relative;
    display: none;
  }
  &__text {
    @extend .request__text;
    text-align: left;
  }
  & > form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    font-size: $fz-title;
    line-height: 36px;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 40px;
    text-align: center;
  }
  &__subtitle {
    line-height: 30px;
    font-weight: 500;
    margin-bottom: 50px;
    & > span {
      font-weight: 700;
      text-transform: uppercase;
      color: #db9b01;
    }
  }
  &__input-wrapper {
    margin-bottom: 15px;
    max-width: 370px;
    width: 100%;
    position: relative;
    &.user-icon::before, &.phone-icon::before {
      opacity: .25;
      display: block;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 35px;
    }
    &.user-icon::before {
      content: url(../img/icon/actions/user-black.png);
    }
    &.phone-icon::before {
      content: url(../img/icon/actions/phone-black.png);
    }
  }
  &--left-hand &__input {
    background-color: #f7f7f7;
  }
  &__input {
    background-color: #ffffff;
    border-radius: 10px;
    border: none;
    width: 100%;
    padding: 30px 30px 30px 100px;
    outline: none;
    transition: box-shadow .25s;
    &:focus {
      box-shadow: 0 0 12px 2px rgba(0, 0, 0, 0.6);
    }
    &::placeholder {
      color: rgba(0, 0, 0, 0.25);
      font-size: 1.125rem;
      line-height: 48px;
      font-weight: 500;
    }
  }
  &__btn {
    margin-top: 45px;
    max-width: 385px;
    font-size: 1.375rem;
    line-height: 27px;
    margin-bottom: 20px;
  }
  &__policy {
    font-size: 0.75rem;
    line-height: 12px;
    font-weight: 300;
    color: #a2a2a2;
    max-width: 250px;
  }
  &__footer {
    font-style: oblique;
    font-size: 1.125rem;
    line-height: 24px;
    max-width: 275px;
    margin-top: 50px;
    // font-weight: 400;
  }
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 767px) { //TODO thermal and other
  .request {
    background-position: center;
    padding: 22px 0;
    &__img {
      display: none;
    }
    &--left {
      background-position: right bottom;
    }
  }
  .request-form {
    border: 4px solid #ffffff;
    // background-image: none;
    background-position: right 94%;
    background-size: 50px;
    padding: 45px 15px 40px;
    &--left-hand {
      background-image: url(../img/content/thermal-right.png);
    }
    &__img {
      display: block;
      margin-bottom: 20px;
    }
    &__text { 
      font-size: 0.625rem;
      line-height: 11px;
      font-weight: 300;
      color: $accent-lite;
      max-width: 125px;
      left: 18%;
      &::after {
        width: 30px;
        height: 10px;
      }
    }
    &__title {
      font-size: $fz-title-mob;
      line-height: 18px;
      padding: 0 7px 0;
    }
    &__subtitle {
      font-size: $fz-default-mob;
      line-height: 14px;
      margin-bottom: 25px;
    }
    & > form {}
    &__input-wrapper {
      margin-bottom: 10px;
      &.user-icon::before, &.phone-icon::before {
        transform: scale(0.7) translateY(-50%);
      }
    }
    &__input {
      padding: 15px 20px 15px 30%;
      &::placeholder {
        font-size: $fz-default-mob;
      }
    }
    &__btn {
      margin-top: 10px;
      margin-bottom: 10px;
      font-size: 0.6875rem;
      line-height: 13px;
      padding: 12px 28px;
    }
    &__policy {
      font-size: 0.375rem;
      max-width: 125px;
      line-height: 6px;
    }
    &__footer {
      display: none;
    }
  }
}
@media (max-width: 480px) {}
@media (max-width: 320px) {}